@font-face {
  font-family: "Proxima Nova";
  src: url("../fonts/ProximaNova/ProximaNova-Regular.eot");
  src: local("Proxima Nova Regular"), local("ProximaNova-Regular"),
    url("../fonts/ProximaNova/ProximaNova-Regular.eot?#iefix")
      format("embedded-opentype"),
    url("../fonts/ProximaNova/ProximaNova-Regular.woff") format("woff"),
    url("../fonts/ProximaNova/ProximaNova-Regular.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Proxima Nova";
  src: url("../fonts/ProximaNova/ProximaNova-Bold.eot");
  src: local("Proxima Nova Bold"), local("ProximaNova-Bold"),
    url("../fonts/ProximaNova/ProximaNova-Bold.eot?#iefix")
      format("embedded-opentype"),
    url("../fonts/ProximaNova/ProximaNova-Bold.woff") format("woff"),
    url("../fonts/ProximaNova/ProximaNova-Bold.ttf") format("truetype");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Neometric";
  src: url("../fonts/Neometric/Neometric-Bold.eot");
  src: local("Neometric Bold"), local("Neometric-Bold"),
    url("../fonts/Neometric/Neometric-Bold.eot?#iefix")
      format("embedded-opentype"),
    url("../fonts/Neometric/Neometric-Bold.woff2") format("woff2"),
    url("../fonts/Neometric/Neometric-Bold.woff") format("woff"),
    url("../fonts/Neometric/Neometric-Bold.ttf") format("truetype");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Neometric Alt";
  src: url("../fonts/Neometric/NeometricAlt-Regular.eot");
  src: local("Neometric Alt Regular"), local("NeometricAlt-Regular"),
    url("../fonts/Neometric/NeometricAlt-Regular.eot?#iefix")
      format("embedded-opentype"),
    url("../fonts/Neometric/NeometricAlt-Regular.woff2") format("woff2"),
    url("../fonts/Neometric/NeometricAlt-Regular.woff") format("woff"),
    url("../fonts/Neometric/NeometricAlt-Regular.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
